<template>
  <span :class="[classname, 'umis-html__container']" ref="countRef"/>
</template>
<script>
import {defineComponent, getCurrentInstance, onMounted, onBeforeUnmount} from 'vue';
import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import mixinProps from '../props/count';

export default defineComponent({
  name: 'MisCount',
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    let instance;
    const startCountUp = async() => {
      try {
        const CountUp = await import(/* webpackChunkName: "countup", webpackPrefetch: true */ 'countup');
        instance = new CountUp.default(proxy.$refs.countRef, 0);
        const value = proxy.$expressionEval(props.value, state.data);
        if (!instance.error) {
          instance.update(value);
        } else {
          proxy.$message.error(instance.error);
          console.error(instance.error);
        }
      } catch(e) {
        proxy.$message.error(e.message);
        console.error(e);
      }
    };
    onMounted(() => {
      startCountUp();
    });
    onBeforeUnmount(() => {
      instance.pauseResume();
    });
    return {
      path: ctx.attrs.path,
      ...useDerivedProps(),
      state,
    };
  },
});
</script>
